
.u-listStyle {
  // https://www.1keydata.com/css-tutorial/tw/list.php
  // disc ()
  // circle ()
  // square () 
  &-disc {
    list-style-type: disc;
  }
  &-circle {
    list-style-type: circle;
  }
  &-square {
    list-style-type: square;
  }

  // list-style-position
  &-inside {
    list-style-position: inside;
  }
  &-outside {
    list-style-position: outside;
  }
}
