/*----------  fontSize  ----------*/
// 12, 14, 16, 20, 24,
.u-fontSize {
  &-ss {
    font-size: 12px;
  }
  &-s {
    font-size: 14px;
  }
  &-base {
    font-size: 16px;
  }
  &-m {
    font-size: 20px;
  }
  &-l {
    font-size: 24px;
  }
}
